import React from "react";
import s from './Post.module.css'


type PostType ={
    id: number
    message: string
    likesCount: number
}

const Post = (props: PostType) => {
    return (
        <div className={s.items} key={props.id}>
            <img
                src="data:image/jpeg;base64,"
                alt=""/>
            {props.message}
            <div>
                <span>{props.likesCount}</span>
            </div>
        </div>


    )
}
export default Post;